var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',[_c('h1',[_vm._v("About Me.")]),_c('div',{staticClass:"main_img",staticStyle:{"display":"table","width":"auto","position":"relative","float":"right","max-width":"100%","clear":"right","margin-top":"0px","*margin-top":"0px"}},[_c('img',{staticStyle:{"margin":"5px 5px 5px 5px","border-width":"1px","padding":"3px","max-width":"100%"},attrs:{"src":"http://www.nicholasjcrow.com/uploads/9/1/4/4/91443340/14462794-1103916676310769-9124752866865505072-n.jpg","alt":"Image"}}),_c('span',{staticStyle:{"display":"table-caption","caption-side":"bottom","font-size":"90%","margin-top":"10px","margin-bottom":"10px","text-align":"center"}},[_vm._v("Hey look, its me.")])]),_c('p',[_vm._v("Currently, I am a Senior at University of California, San Diego. While in school, I have been very passionate about Computer Science and Virtual Reality. Over the past four years, I have found many new passions and have done many fun projects.")]),_c('p',[_vm._v("I started out in a little town in Northern California called San Lorenzo, also known as the little town that nobody has ever heard of. I went to middle school and high school at KIPP, the Knowledge is Power Program, which is a ring of charter schools throughout America that promote education for the less fortunate. It was in middle school that my passion for computers and technology came, because my teachers all fostered my interest and taught me all about computers.")]),_c('p',[_vm._v("After high school, I came to UC San Diego, which was a big change for me. It was like a fresh start, getting to meet lots of new people who all had the same interests as me, people to foster my dreams with. As my time here passes by, I keep realizing that I would never have gotten as far as I have today without UCSD, which is an amazing school.")]),_c('p',[_vm._v("My interests lie in both Computer Science and Business, which is one of the reasons I started a company while still in middle school called Lightning Proxies. I ran the company for about 6 years, and recently had to shut down due to school, because I needed to focus on my studies.")]),_c('p',[_vm._v("While at UCSD, I have gotten the chance to attend many hackathons, which are 36 hour programming competitions put on for programmers and other people to show what they’re made of and come up with ideas to change the world. Over my time so far, I have gone to hackathons in San Diego, Santa Barbara, Los Angeles, Irvine, San Mateo, and even Arizona!")]),_c('p',[_vm._v("The past year or so, my interests began to lie within the field of Virtual Reality and Augmented Reality. I began to work with the HTC Vive, and built some awesome things. I did a research project under one of my professors in which we used the Vive and Leap Motion in order to allow a user to ‘feel’ a spider crawling on their arm. We used the haptic feedback from a tens unit (an electrotherapy device) to stimulate the users arm so it felt like a spider was walking on it. Along with that project, I became involved in the Virtual Reality club, and became the Historian for my sophomore year. During the past summer, I worked for three different VR companies. The first, VR Medical Center, has been around for 20 years and is building experiences to help people fight their phobias in VR. The next one is Nanome, which is a Nanoparticle simulation experience. The third is a stealth mode startup which is working with smell-based VR.")])])}]

